import React, {Component} from "react";
import {Runtime, Inspector} from "@observablehq/notebook-runtime";
import notebook from "hackaviz-toulouse-2018";



class Hackaviz extends Component {

  animationRef = React.createRef();


  componentDidMount() {
    console.log('please wait');
    Runtime.load(notebook, (cell) => {
      if (cell.name === "carte") {
        return new Inspector(this.animationRef.current);
      }

    });
  }


  render() {
    console.log('loading data');
    return (
      <div className="Hackaviz" ref={this.animationRef}>

      </div>
    );
  }
}

export default Hackaviz;